export default {
	data:function(){
		return {
			obje:''
		}
	},
	mounted(){
		this.logan()
	},
	methods:{
		logan:function(){
			uni.request({
				method:'GET',
				url:'https://unidemo.dcloud.net.cn/api/news',
				success: (res) =>{
					if(res.statusCode == 200){
						this.obje=res.data
					 console.log(res.data)	
					}else{
						console.log(Error)
					}
				}
			})
		},
		click:function(item,index){
			var index = index
			localStorage.setItem('newsand',JSON.stringify(this.obje[index]))
			uni.navigateTo({
				url:'../Newsdetail/newsdetail',
			})
			console.log(index)
		}
	}
}
